@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"); }

[v-cloak] {
  display: none; }

.tooltip {
  display: block !important;
  z-index: 10000; }
  .tooltip .tooltip-inner {
    background: black;
    color: white;
    border-radius: 16px;
    padding: 5px 10px 4px; }
  .tooltip .tooltip-arrow {
    width: 0;
    height: 0;
    border-style: solid;
    position: absolute;
    margin: 5px;
    border-color: black;
    z-index: 1; }
  .tooltip[x-placement^="top"] {
    margin-bottom: 5px; }
    .tooltip[x-placement^="top"] .tooltip-arrow {
      border-width: 5px 5px 0 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      bottom: -5px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0; }
  .tooltip[x-placement^="bottom"] {
    margin-top: 5px; }
    .tooltip[x-placement^="bottom"] .tooltip-arrow {
      border-width: 0 5px 5px 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-top-color: transparent !important;
      top: -5px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0; }
  .tooltip[x-placement^="right"] {
    margin-left: 5px; }
    .tooltip[x-placement^="right"] .tooltip-arrow {
      border-width: 5px 5px 5px 0;
      border-left-color: transparent !important;
      border-top-color: transparent !important;
      border-bottom-color: transparent !important;
      left: -5px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0; }
  .tooltip[x-placement^="left"] {
    margin-right: 5px; }
    .tooltip[x-placement^="left"] .tooltip-arrow {
      border-width: 5px 0 5px 5px;
      border-top-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      right: -5px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0; }
  .tooltip.popover .popover-inner {
    background: #f9f9f9;
    color: black;
    padding: 24px;
    border-radius: 5px;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1); }
  .tooltip.popover .popover-arrow {
    border-color: #f9f9f9; }
  .tooltip[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    transition: opacity .15s, visibility .15s; }
  .tooltip[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    transition: opacity .15s; }

.button {
  display: inline-block;
  vertical-align: middle;
  padding: 0.5em 1.5em;
  font-size: 16px;
  color: #444444;
  border: 1px solid #444444;
  border-radius: 3px;
  background-color: #fafafa;
  cursor: pointer;
  transition: background-color 0.3s ease; }
  .button:hover {
    background-color: #f0f0f0; }

.page-wrapper {
  font-family: "Roboto", "Arial", sans-serif; }

.users__empty {
  padding: 10px 0;
  text-align: center; }

.users__table {
  margin: 0 auto;
  border-collapse: collapse;
  border-right: 1px solid #444444;
  border-left: 1px solid #444444; }

.users__caption {
  font-size: 18px;
  padding: 0.5em 0; }

.users__header-row {
  color: #ffffff;
  background-color: #444444; }
  .users__header-row th {
    padding: 0.7em 2em;
    border-right: 1px solid #ffffff; }
    .users__header-row th:last-child {
      border-right: none; }

.users__body-row {
  border-bottom: 1px solid #444444;
  background-color: #fafafa;
  transition: background-color 0.3s ease; }
  .users__body-row:nth-child(odd) {
    background-color: #f0f0f0; }
  .users__body-row:hover {
    background-color: #cccccc; }
  .users__body-row td {
    padding: 0.5em 1em;
    border-right: 1px solid #444444; }
    .users__body-row td:last-child {
      border-right: none; }

.users__avatar img {
  display: block;
  border-radius: 50%; }

.users__toggle-button {
  display: block;
  margin: 0 auto;
  margin-top: 20px; }

.user-edit__empty {
  text-align: center; }

.user-edit__data {
  margin: 0 auto;
  border-collapse: collapse;
  border-right: 1px solid #444444;
  border-left: 1px solid #444444; }

.user-edit__data-row {
  border-bottom: 1px solid #444444;
  background-color: #fafafa;
  transition: background-color 0.3s ease; }
  .user-edit__data-row:nth-child(odd) {
    background-color: #f0f0f0; }
  .user-edit__data-row td {
    padding: 0.5em 1em;
    border-right: 1px solid #444444; }
    .user-edit__data-row td:last-child {
      border-right: none; }

.user-edit__data-value input, .user-edit__data-value textarea {
  box-sizing: border-box;
  width: 300px;
  padding: 0 5px;
  font-size: 16px;
  border: 1px solid #949cff;
  border-radius: 3px; }

.user-edit__data-value input {
  height: 26px; }

.user-edit__data-value textarea {
  min-height: 100px;
  padding: 5px;
  resize: vertical; }
